.MainContainer{
    width: 100vw;
    max-width: 100%;
}
.ImagesHeading{
    width: 50%;
    margin: auto;
    padding: 1vmax 0;
    text-align: center;
    border-bottom: 4px solid var(--backgroundColor);
    font: 600 1vmax 'Roboto';
}
.BannerContainer{
    width: 80%;
    height: 16vmax;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 1vmax;
    background-image: url('../Header/Images/wp2612016-full-hd-wallpaper-downlord.jpg');
    background-position: center;
    background-repeat: no-repeat;
    margin: 0 auto;
    

}
.BannerContainer img{
    width: 5vmax;
}
.BannerContainer > div {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.BannerContainer > div > input{
    width: 70%;
    /* height: 2vmax; */
    border: 1px solid white;
    background:none ;
    padding: 1vmax;
    /* border-style: none; */
    direction: none;
    font: 300 1.2vmax "Roboto";
    color: var(--textColorLight);
    box-shadow: .5px .5px 10px gray;
}

.BannerContainer > div > button{
    width: 5vmax;
    padding: 1vmax;
    font: 300 1.2vmax "Roboto";
    color: var(--textColor);
    background-color: var(--backgroundColorLight);
    border: 1px solid white;
    box-shadow: .5px .5px 10px gray;
    /* background:none ; */

}

.BannerContainer > p{
    font: 600 1vmax cursive;
    color: var(--textColor);
}
.ImagesContainer1{
    width: 100vw;
    max-width: 100%;
    padding: 2vmax 1vmax ;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
}


@media screen and (max-width: 600px) {
    .ImagesHeading{
        width: 70%;
        margin: 1.8vmax auto;
        padding: 1vmax 0;
        text-align: center;
        border-bottom: 2px solid var(--backgroundColor);
        font: 600 1vmax 'Roboto';
    }
    .ImagesHeading > h2{
        font: 600 2vmax 'Roboto';

    }

    .ImagesContainer1{
        width: 100vw;
        max-width: 100%;
        padding: 2vmax .5vmax ;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
    }

    .BannerContainer{
        width: 100%;
        height: 20vmax;
    }
    .BannerContainer img{
        width: 9vmax;
    }

    .BannerContainer > div > input{
        width: 80%;
        font: 300 1.8vmax "Roboto";
      
    }
    .BannerContainer > div > button{
        width: 7vmax;
        font: 300 1.8vmax "Roboto";
       
    }
    .BannerContainer > p{
        font: 600 1.8vmax cursive;
        color: var(--textColor);
    }
}