.ImageBoxContainer{
    width: 22vmax;
    /* border: 1px solid black; */
    margin: .15vmax;
}

.ImageBoxContainer > img{
    width: 100%;
}

.ImageBoxContainer > div{
    /* width: 6vmax; */
    display: flex;
    justify-content: space-between;
    font: 300 1vmax 'Roboto';
    align-items: center;
    color: var(--textColor);
    padding: .5vmax 0;
}
.ImageBoxContainer > div > div > a ,
.ImageBoxContainer > div > div{
    display: flex;
}
.ImageBoxContainer > div > div > a> div ,
.ImageBoxContainer > div > div > div{
    /* height: 5vmax; */
    background-color: var(--backgroundColorLight);
    /* border: 1px solid gray; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  
    margin: .2vmax;
    padding: .1vmax .3vmax;
    transition: all 1s;
}
a{
    text-decoration: none;
    color: var(--textColor);
}
.ImageBoxContainer > div > div > a > div> svg:hover,

.ImageBoxContainer > div > div > div> svg:hover{
    color: black;
}

.ImageBoxContainer > div > div> a > div > svg,
.ImageBoxContainer > div > div> div > svg{

    color: var(--textColor);
    font-size: 1.4vmax;
    padding: 0;
}

@media screen and (max-width: 600px) {
    .ImageBoxContainer{
        width: 100%;
        margin: 1vmax;
    }
    .ImageBoxContainer > div{
    
        font: 300 2.5vmax 'Roboto';
        color: var(--textColor);
    }
.ImageBoxContainer > div > div > a > div ,
    .ImageBoxContainer > div > div > div{
      
        margin: .4vmax;
        padding: .4vmax 1.5vmax;
    }
    .ImageBoxContainer > div > div> a > div > svg,
    .ImageBoxContainer > div > div>  div > svg{
        color: var(--textColor);
        font-size: 3vmax;
        padding: 0;
    }
}