    textColor: red;
} */
.main{
    max-width: 100%;
}

.headerContainer{
    width: 100vw;
    height: 4vmax;
    max-width: 100%;
    display: flex;
    padding: 5px 2vmax;
    background-color: var(--backgroundColor);
    box-sizing: border-box;
    /* position: fixed; */
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
}
.headerLogo > img{
    width: 110px;
}

.headerMenuBox{
    display: flex;
    justify-content: center;
    align-items: center;
}
.headerMenuBox > a{
    text-decoration: none;
    
}
.headerMenu{
    /* width: vmax; */
    cursor: pointer;
    padding: 1vmax;
    /* background-color: aqua; */
    margin: 0 1vmax;
    color: var(--textColorLight);
    font: 300 1vmax cursive;
    transition: all 1s;
}
.btnn{    
    cursor: pointer;
    padding: .6vmax .9vmax;
    background-color: var(--backgroundColorLight);
    border-radius: 2vmax;
    margin: auto;
    color: var(--textColorLight);
    font: 300 1vmax cursive;
    transition: all 1s;
    border: 2px solid var(--textColorLight);

}
.btnn:hover{
    box-shadow: 2px 2px 8px white;
    border: 2px solid gray;
    color: gray;
}
.headerMenu:hover{

    color: rgb(223, 182, 182);

    

}

.categorysContainer{
    width: 100vw;
    
    max-width: 100%;
    box-sizing: border-box;

    display: flex;
    padding: .6vmax;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

}
.categoryBox{
    min-width: 2vmax;
    /* height: 2vmax; */
    max-width: 100%;
    padding: .6vmax;
    margin: .2vmax;
    color: gray;
    font: 400 1vmax 'Roboto' ;
    transition: all 1s;
    box-sizing: border-box;

    cursor: pointer;
    
}
.categoryBox:hover{
    transform: scale(1.1);
    color: black;
}
.categoryBoxColor1{
    border: 3px solid green;
}.categoryBoxColor2{
    border: 3px solid gray;
}.categoryBoxColor3{
    border: 3px solid red;
}.categoryBoxColor4{
    border: 3px solid orange;
}.categoryBoxColor5{
    border: 3px solid black;
}

.CategoryHoverBox{
    width: 51vw;
    max-width: 100%;
    /* height: 60vh; */
    background-color: azure;
    position: absolute;
    right: 8vmax;
    padding: 1vmax;
    box-sizing: border-box;
margin-top: 10px;   
 box-shadow: 1px 2px 8px gray;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    overflow: scroll;

}


.hide{
    display: none;
}
.Point{
    width: 20px;
    height: 20px;
    background-color: azure;
    rotate: 40deg;
    position: absolute;
    right: 70px;
    top: -8px;
    /* transform: rotate(90deg) ; */
}

.categoryDiv{
    width: 120px;
    height: 120px;
    border: 1px solid gray;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    font: 300 1.2vmax "Roboto";
    margin: .8vmax;
    color: var(--textColor);

}
.categoryDiv > img{
    width: 65px;
    transition: all .5s;
}

.categoryDiv > img:hover{
    transform: scale(1.2);
}

.categoryMainDiv{
    
    display: flex;
    flex-wrap: wrap;
}
.menuIcon{
    display: none;
}

@media screen and (max-width: 600px) {
    .headerContainer{
        max-width: 100%;
        height: 5vmax;
    }
    .headerLogo > img{
        width: 40px;
    }
    .headerMenuBox{
        position: absolute;
        top: 5vmax;
        right: 1px;
        width: 60%;
        height: 100vh;
        padding: 1vmax 0;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        color: var(--textColor);
        background-color: var(--backgroundColorLight);

    }
    .menuIcon{
        display: block;
        width: 20px;
        height: 20px;
        /* border: 2px solid white; */
        position: absolute;
        top: 2px;
        right: 5px;
    }
    .menuIcon > svg{
        /* margin-right:; */
        color: var(--textColorLight);
        font-size: 3.6vmax;
    }
    .headerMenu{
        width: 99%;
        height: 8vmax;
        margin: 0.1vmax;
        border-bottom: 1px solid gray;
        font: 400 2.2vmax "Roboto";
        /* padding: 0 1vmax; */
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .headerMenu:last-child{
        
        border-bottom: none;
  
    }
.hide2{
    display: none;
}
.categoryBox{

    font: 300 2vmax "Roboto";
    padding: 1vmax;
}
.btnn{
    padding: 1vmax ;
    /* height: 100%; */
    width: 90%;
    text-align: center;
    background-color: var(--backgroundColor);
    border-radius: 4vmax;
    margin: 1vmax auto;
    color: var(--textColorLight);
    font: 300 1.9vmax cursive;
    transition: all 1s;
    border: 2px solid var(--textColorLight);
}
.Point{
    display: none;
}
.CategoryHoverBox{
    width: 100vw;
    right: 0;
}
.categoryDiv{
    width: 100px;
    height: 100px;
    font: 300 1.8vmax "Roboto";

}
.categoryDiv > img{
    width: 8vmax;
    transition: all .5s;
}
  }