.uploadContainer{
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 50;
    background-color: var(--backgroundColorLight);
}
.uploadContainer >div:first-child{
    width: 65vw;
    padding: .5vmax 1vmax;

}
.uploadContainer > div > h3{
    font: 500 2.5vmax "Roboto";
    color: var(--textColorLight);
}

.uploadBox{
    width: 65vw;
    display: flex;
    border: 1px solid gray;
    padding: 3vmax .5vmax;
    box-shadow: 1px 1px 8px gray;
    background-color: white;
}
.uploadBox > div{
    width: 50%;
    /* border: 1px solid gray; */
    /* display: flex; */
    margin: auto;

}

.uploadBox > div > form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* border: 1px solid gray; */

    /* align-items: center; */
}
.uploadBox > div > form > input::file-selector-button,
.uploadBox > div > form >select,
.uploadBox > div > form >button,
.uploadBox > div > form >input{
    width: 80%;
    margin: 1vmax 0;
    padding: 1vmax;
    font: 400 1.4vmax "Roboto";

}
.C-btn{
    font: 300 2.2vmax "Roboto" !important;
    color: white;
    background-color: var(--backgroundColor);
    transition: all .5s;
}
.C-btn:hover{
    background-color: var(--backgroundColorLight);
}

.uploadBox > div > img{
    width: 90%;
    max-height: 285px;
    margin: auto;
}
@media screen and (max-width:600px) {
.uploadContainer{
    padding: 1vmax;
}
    .uploadBox{
        flex-direction: column;
        width: 98vw;
        padding: 4vmax 1vmax;
        min-height: 90vh;
    }
    .uploadContainer >div:first-child{
        width: 98vw;
        padding: .5vmax 1vmax;
    
    }
    .uploadContainer > div > h3{
        font: 500 3.5vmax "Roboto";
    }
    .uploadBox > div{
        width: 100%;
        height: 50%;
    }
    .uploadBox > div > form >select,
.uploadBox > div > form >button,
.uploadBox > div > form >input{
    width: 80%;
    margin: 1.5vmax 0;
    padding: 2vmax;
    font: 400 1.9vmax "Roboto";

}

.uploadBox > div > img{
    width: 100%;
    margin: auto;
}
}


